import '../styles/component.scss';

document.addEventListener('DOMContentLoaded', function() {

	const openSelectBox = (item) => {
		item.classList.remove('closed')
		item.classList.add('open');

	};

	const closeSelectBox = (item) => {
		item.classList.remove('open');
		item.addEventListener(
			'webkitTransitionEnd otransitionend oTransitionEnd msTransitionEnd transitionend',
			({ target }) => {
				target.removeClass('closed');
			},
			{
				once: true,
			}
		);

	};

	const selectboxOpeners = document.body.querySelectorAll('.js-rstuning__selectbox__opener');
	[...selectboxOpeners].forEach(selectboxOpener => {
		selectboxOpener.addEventListener(
			'click',
			(event) => {
				event.stopPropagation();

				var selectBox = event.target.closest('.js-rstuning__selectbox');

				if (selectBox.classList.contains('open'))
				{
					closeSelectBox(selectBox);
				}
				else
				{
					const selectBoxOpened = [...document.body.querySelectorAll('.js-rstuning__selectbox.open')]
						.filter(item => item !== selectBox);

					selectBoxOpened.forEach(selectBox => {
						closeSelectBox(selectBox);
					});
					openSelectBox(selectBox);
				}
			}
		);
	});

	const selectBoxOptions = document.body.querySelectorAll('.js-rstuning__selectbox__option');
	[...selectBoxOptions].forEach(selectBoxOption => {
		selectBoxOption.addEventListener(
			'click',
			(event) => {
				event.preventDefault();
				const selectBoxOption = event.target;

				if (selectBoxOption.classList.contains('active'))
				{
					const selectBox = selectBoxOption.closest('.js-rstuning__selectbox');
					selectBox.classList.toggle('open')
				}
				else
				{
					var selectBoxSelect = selectBoxOption.closest('.js-rstuning__selectbox__select'),
						selectBoxOptionInfo = selectBoxOption.closest('.js-rs_option_info'),
						selectBoxInput = selectBoxOptionInfo.querySelector('input'),
						selectBoxValue = selectBoxOptionInfo.querySelector('.js-rstuning__selectbox__value');

					selectBoxInput.value = selectBoxOption.getAttribute('data-value');
					selectBoxValue.innerHTML = selectBoxOption.innerHTML;

					selectBoxInput.dispatchEvent(new Event('change'));
					selectBoxSelect.querySelector('.js-rstuning__selectbox__option.active').classList.remove('active');

					selectBoxOption.classList.add('active');

					const selectBox = selectBoxOption.closest('.js-rstuning__selectbox');
					selectBox.classList.toggle('open');

					return false;
				}
			}
		);
	});

	document.body.addEventListener(
		'click',
		(event) => {

			const tuningRoot = document.body.querySelector('.js-rstuning');
			const selectBoxOpened = tuningRoot.querySelectorAll('.js-rstuning__selectbox.open');

			if (tuningRoot.classList.contains('open'))
			{
				const selectBox = event.target.closest('.js-rstuning__selectbox.open');
				if (selectBox)
				{}
				else
				{
					[...selectBoxOpened].forEach(selectBox => {
						closeSelectBox(selectBox);
					});
				}
			}
			else
			{
				[...selectBoxOpened].forEach(selectBox => {
					closeSelectBox(selectBox);
				});
			}
		}
	);
});
